import {
    Header,
    Content,
    Divider,
    Form,
    Button,
    ButtonToolbar,
    Table,
    Modal,
    toaster,
    Message,
} from "rsuite";
import RemindIcon from "@rsuite/icons/legacy/Remind";
import React, { useEffect, useState } from "react";
import axios from "axios";

const MessageAddNewGroupSuccessful = (
    <Message
        showIcon
        type="success"
        closable
        style={{ marginBottom: "15px", fontWeight: "bold" }}
    >
          
    </Message>
);

const MessageAddNewGroupFailed = (
    <Message
        showIcon
        type="error"
        closable
        style={{ marginBottom: "15px", fontWeight: "bold" }}
    >
         
    </Message>
);

const AdminRegGroup = ({}) => {
    const [nameGroup, setNameGroup] = useState("");
    const [speciality, setSpeciality] = useState("");
    
    const [data, setData] = useState([]);

    const [open, setOpen] = useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const responseTable = axios.get("/groups/all");
    console.log(responseTable);

    useEffect(()=>{
        axios.get("/groups/all")
        .then(response => {
                setData(response.data);
            }
        )
        .catch(error => {
            console.error('   :', error);
        });
    }, []);

    const onChangeGroup = (value) => {
        setNameGroup(value);
    };

    const onChangeSpeciality = (value) => {
        setSpeciality(value);
    };

    const handleSubmit = (e) => {
        handleOpen();
    };

    const handleErase = (e) => {
        setNameGroup("");
        setSpeciality("");
    };

    const requestPost = async (e) => {
        try {
            const response = await axios.post(
                "/groups/add",
                { nameGroup, speciality },
                {
                    headers: {
                        "Content-Type": "application/json",
                    },
                }
            );
            if (response.status === 200) {
                toaster.push(MessageAddNewGroupSuccessful, {
                    placement: "topCenter",
                    duration: 5000,
                });
            }
        } catch (error) {
            toaster.push(MessageAddNewGroupFailed, {
                placement: "topCenter",
                duration: 5000,
            })
            console.error(error);
        }
    };

    return (
        <>
            <Header>
                <h2
                    style={{
                        color: "white",
                        marginTop: "20px",
                        marginLeft: 10,
                    }}
                >
                      
                </h2>
            </Header>
            <Content style={{ color: "white" }}>
                <Divider />
                <Content style={{ marginLeft: 15, paddingRight: 30 }}>
                     ,      .
                       .   
                     ,  ` .  
                          -.  
                            -.
                </Content>
                <Divider />
                <h4
                    style={{
                        color: "white",
                        marginTop: "20px",
                        marginLeft: 10,
                    }}
                >
                     
                </h4>
                <Form style={{ marginLeft: 15, marginTop: 20 }}>
                    <Form.Group controlId="name-group">
                        <Form.ControlLabel> </Form.ControlLabel>
                        <Form.Control
                            name="name-group"
                            value={nameGroup}
                            onChange={onChangeGroup}
                        />
                        <Form.HelpText>' </Form.HelpText>
                    </Form.Group>
                    <Form.Group controlId="speciality-group">
                        {/* MAYBE UPDATE THIS FORM GROUP  */}
                        <Form.ControlLabel>
                             
                        </Form.ControlLabel>
                        <Form.Control
                            name="speciality-group"
                            value={speciality}
                            onChange={onChangeSpeciality}
                        />
                        <Form.HelpText>' </Form.HelpText>
                        {/*  */}
                    </Form.Group>

                    <ButtonToolbar>
                        <Button
                            appearance="primary"
                            onClick={handleSubmit}
                            style={{ backgroundColor: "#9557e0" }}
                        >
                             
                        </Button>
                        <Button appearance="default" onClick={handleErase}>
                             
                        </Button>
                    </ButtonToolbar>

                    <Modal open={open} onClose={handleClose} role="alertdialog">
                        <Modal.Header>
                            <Modal.Title>
                                {" "}
                                <RemindIcon
                                    style={{ color: "#ffb300", fontSize: 24 }}
                                />
                                &nbsp; ,    
                                ?
                            </Modal.Title>
                        </Modal.Header>
                        <Modal.Body style={{ color: "white" }}>
                             : {nameGroup} <br></br>
                             : {speciality}
                        </Modal.Body>
                        <Modal.Footer>
                            <Button
                                onClick={() => {
                                    handleClose();
                                    requestPost();
                                }}
                                style={{ backgroundColor: "#9557e0" }}
                                appearance="primary"
                            >
                                ,    
                            </Button>
                            <Button onClick={handleClose} appearance="subtle">
                                ,  
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </Form>
                <Divider />
                <h4 style={{ marginLeft: 15, paddingRight: 30 }}>
                         -  
                </h4>
                <Divider />
                <Table showHeader={true} width={1632} height={400} data={data}>
                    <Table.Column width={408} align="center" fixed>
                        <Table.HeaderCell>ID </Table.HeaderCell>
                        <Table.Cell dataKey="idGroup" />
                    </Table.Column>
                    <Table.Column width={408} align="center">
                        <Table.HeaderCell> </Table.HeaderCell>
                        <Table.Cell dataKey="name" />
                    </Table.Column>
                    <Table.Column width={408} align="center">
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.Cell dataKey="speciality" />
                    </Table.Column>
                    <Table.Column width={408} align="center">
                        <Table.HeaderCell> </Table.HeaderCell>
                        <Table.Cell dataKey="serialKey" />
                    </Table.Column>
                </Table>
            </Content>
        </>
    );
};

export default AdminRegGroup;
